.category-cont {
    margin: 78px 0 80px;
    .top-banner,
    .issue-and-deal-banner {
        margin-bottom: 50px;
        .thumb-list  {
            overflow: hidden;
        }
        .item {
            margin-right: 20px;
        }
    }
    .issue-and-deal-banner {
        margin-top: 70px;
    }
    .banner-list {
        margin-bottom: 50px;
        &::after {
            content: '';
            display: table;
            clear: both;
        }
        li {
            position: relative;
            float: left;
            margin-left: 21px;
            &:first-child {
                margin-left: 0;
            }
        }
    }
    hr {
        border: 2px solid #1f1f1f;
        margin: 75px 0 50px;
    }
}
